// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const Demo = function () {
  return (
    <div>
      <h2>Show Clean Button</h2>
      <Datepicker
        selected={new Date()}
        placeholder={'点击选择日期'}
        showClean
      />
      <span
        style={{
          display: 'inline-block',
          width: '100px'
        }}
      ></span>
      <Datepicker
        selected={new Date()}
        placeholder={'点击选择日期'}
        showClean
        showIcon
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo14')
);
